@import url('https://fonts.googleapis.com/css?family=Audiowide');

.endzone-product-div {
    display: inline-flex;
    text-align: left;
    margin: 2.5%;
    width: 95%;
    height: 20%;
    background-color: #252830;
    border: .1vh solid #3b3e45; 
    border-radius: 1vh;
    font-family: 'Audiowide';
    min-width: 300px;
}

.product-text {
    display: inline-block;
    text-align: center;
    width: 75%;
    height: 100%;

}

.product-header {
    display: inline-block;
    height: 25%;
    text-align: center;
}

.product-header h3 {
    display: inline-block;
    color: #0984e3;
    font-size: 2.25vh;
    font-weight: 100;
    height: auto;
    width: auto;
    border-bottom: .1vh solid white;
}

.product-info {
    text-align: center;
    height: auto;
    display: inline-flex;
    width: 100%;
    color: white;
}

.product-info p {
    width: 100%;
    height: 100%;
    padding: 1vh;
    font-size: 2vh;
    text-align: center;
    overflow-y: hidden;
    font-weight: 100;
}

.icon-div {
    display: inline-flex;
    width: 25%;
    height: 100%;
    justify-content: center;
    align-items: center;
    background-color: #07080a;
    border-top-left-radius: 1vh;
    border-bottom-left-radius: 1vh;
}

.tool-icon {
    color: white;
}

.endzone-product-div:hover .tool-icon{
    color: #0984e3;
    transform: scale(1.2);

    transition: ease-in-out .25s;
}

.endzone-product-div:hover {
    animation: content_pulse 2s infinite;
}

@keyframes content_pulse {
	0% {
		box-shadow: 0 0 0 0 rgba(9, 132, 227, .75);
	}

    33% {
		box-shadow: 0 0 0 .25vh rgba(9, 132, 227, .5);
	}

	66% {
		box-shadow: 0 0 0 .5vh rgba(9, 132, 227, .25);
	}

	100% {
		box-shadow: 0 0 0 .75vh rgba(9, 132, 227, 0);
	}
}

