@import url('https://fonts.googleapis.com/css?family=Audiowide');

body {
    overflow-x: hidden;
    overflow-y: scroll;
    background-color: #0d1019;
    font-family: 'Audiowide';
    margin: 0;
    padding: 0;
}

.test {
    color: white;
}

.product-group-header {
    width: 100%;
    border-bottom: .25vh solid #0984e3;
}

.product-group-header h3 {
    font-size: 3vh;
    color: white;
}

.products-div {
    display: inline-flex;
    width: 100%;
    height: auto;

}

.products-page-div {
    width: 100%;
    height: 100%;
    text-align: center;
}

.products-div-left, .products-div-right {
    display: inline-flex;
    width: 50%;
    height: auto;

}

.products-div-left-left, .products-div-left-right, .products-div-right-left, .products-div-right-right {
    display: inline-block;
    width: 50%;
    height: auto;
    opacity: .5;
}

.products-div-left-left:hover {
    animation: opacity_in .5s;
    opacity: 1;
}

.products-div-left-right:hover {
    opacity: 1;
    animation: opacity_in .5s;
}

.products-div-right-left:hover {
    opacity: 1;
    animation: opacity_in .5s;
}

.products-div-right-right:hover {
    opacity: 1;
    animation: opacity_in .5s;
}


@keyframes opacity_in {
	0% {
        opacity: .25;
	}

    33% {
        opacity: .5;
	}

	66% {
        opacity: .75;
	}

	100% {
        opacity: 1;
	}
}

@media (max-width: 1200px) {
    .products-div-left, .products-div-right {
        display: inline-block;
        width: 100%;
    }

    .products-div-left-left, .products-div-left-right, .products-div-right-left, .products-div-right-right {
        display: inline-block;
        width: 100%;
    }
}

@media (max-width: 800px) {
    .products-div {
        width: 75%;
        display: inline-block;
    }

}